import React, { Component } from "react";
import Header from './Header';
import Footer from './Footer';
import Newslist from './Newslist';
import JSON from '../db.json'

class Home extends Component {
  constructor() {
    super()

    this.state = {
      news: JSON,
      filtered: JSON
    }
  }

  
  filterNews(userinput) {
    const output =this.state.news.filter((data) => {
      return data.title.toLowerCase().indexOf(userinput.toLowerCase()) > -1
    })
    this.setState({ filtered:output })
  }


  render() {
    console.log(this.state.news)
    return (

      <React.Fragment>
        <Header userText={(data) => { this.filterNews(data) }} />
        <h1><center>Developer Funnel</center></h1>
        <Newslist newsdata={this.state.filtered} />
        <Footer year='2020' month="Sep" />
      </React.Fragment>

    )
  }
}
export default Home;

